import proj4 from "proj4";
proj4.defs("EPSG:3857");
proj4.defs("EPSG:4326");

const range = [0, 12, 35, 70, 150, 200, 250, 1000];
const colors = [
    'rgb(0,153,102)',
    'rgb(255,222,51)',
    'rgb(255,153,51)',
    'rgb(204,0,51)',
    'rgb(102,0,153)',
    'rgb(126,0,35)',
    'rgb(0,0,0)'];

async function Download(dates) {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/9/aggvalues?&key=654hblgm9gl8367h&time_begin=${dates[0]} 00:00:00&time_end=${dates[dates.length - 1]} 00:00:00&time_interval=1day&indicators=348&limit=30000'`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}

async function XML2JSON(e) {
    const data = e.text().then(e => {
        const result = [];
        const dom = new DOMParser();
        const xml = dom.parseFromString(e, "text/xml");
        const aggvalues = xml.getElementsByTagName("aggvalue");
        aggvalues.forEach((element) => {
            const indicator = element.getAttribute("indicator")
            const time = element.getAttribute("time").substring(0, element.getAttribute("time").length - 3).split(' ')[0];
            const value = Number((element.getElementsByTagName("avg")[0].innerHTML)).toFixed(4);
            const site = element.getAttribute("site");
            const coordinates = element.getElementsByTagName("location")[0];
            const x = coordinates.getAttribute("x");
            const y = coordinates.getAttribute("y");
            result.push({
                site: site, indicator: indicator, time: time, value: value, coordinates: proj4("EPSG:4326", "EPSG:3857", [
                    Number(x),
                    Number(y),
                ])
            })
        })
        return result;
    })
    return data;
}

async function CreateDates() {
    const today = new Date();
    const start = new Date();
    const dates = [];
    start.setDate(start.getDate() - 7)

    while (start.getTime() <= today.getTime()) {
        dates.push(formatDate(start));
        start.setDate(start.getDate() + 1);
    }
    return dates;
}

function formatDate(date) {
    var dd = date.getDate();
    if (dd < 10) dd = "0" + dd;

    var mm = date.getMonth() + 1;
    if (mm < 10) mm = "0" + mm;

    var yy = date.getFullYear();
    if (yy < 10) yy = "0" + yy;

    return yy + "-" + mm + "-" + dd;
}

function GetData(data, time) {
    const result = data.filter(e => { if (e.time == time) return e });
    return result;
}

function CreateLayers(data) {
    const layers = data.map(e => {
        const value = (e.value * 1000).toFixed(0);
        for (let i = 1; i < range.length; i++) {
            if (value >= range[i - 1] && value < range[i]) {
                return {
                    id: e.site,
                    fill: colors[i - 1],
                    radius: 25,
                    value: value,
                    stroke: {
                        color: "white",
                        width: 2
                    },
                    zindex: 10,
                    coordinates: e.coordinates
                }
            }
        }
    })
    return layers;
}

function CreateColoredTooltips(data, dates) {
    const avg = dates.map(d => {
        const timeData = GetData(data, d);
        const length = timeData.length;
        let amount = 0;
        timeData.forEach(t => {
            amount += Number(t.value);
        })
        return ((amount / length) * 1000).toFixed(2);
    });
    const coloredBox = avg.map(a => {
        for (let i = 0; i < colors.length; i++) {
            if (a >= range[i - 1] && a < range[i]) {
                return { color: colors[i - 1], value: a };
            }
        }
    })
    return coloredBox;
}

export { Download, CreateLayers, GetData, CreateDates, CreateColoredTooltips }