<template>
  <div class="container-fluid">
    <div class="row justify-content-center test">
      <div class="col-2 text-center align-self-center">
        <button class="btn btn-success btn-play" @click="Action">
          {{ text }}
        </button>
      </div>
      <div class="col">
        <vue-slider
          :data="dates"
          v-model="selectedIndex"
          :marks="marks"
          @change="UpdateTime"
        ></vue-slider>
      </div>
      <div class="col-2"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["dates", "tooltips"],
  data() {
    return {
      value1: 0,
      selectedIndex: 0,
      min: 0,
      max: 8,
      text: "Play",
      play: false,
      t: [],
      images: [
        {
          id: 1,
          src: "..",
          alt: "Play",
        },
        {
          id: 2,
          src: "",
          alt: "Pause",
        },
      ],
    };
  },
  methods: {
    UpdateTime() {
      this.$emit("update-time", this.selectedIndex);
    },
    Play() {
      const step = 1000;
      this.text = "Pause";
      this.selectedIndex = this.dates[0];
      this.UpdateTime();
      for (let i = 1; i < this.max; i++) {
        this.t.push(
          setTimeout(() => {
            if (this.play) {
              this.selectedIndex = this.dates[i];
              this.UpdateTime();
            }
            if (i == this.max - 1) {
              this.text = "Play";
            }
          }, (i + 1) * step)
        );
      }
    },
    Pause() {
      this.t.forEach((e) => {
        clearTimeout(e);
      });
      this.text = "Play";
    },
    Action() {
      this.play = !this.play;
      if (this.play) {
        this.Play();
      } else {
        this.Pause();
      }
    },
  },
  computed: {
    marks: function () {
      const res = {};
      this.dates.forEach((date, i) => {
        res[date] = {
          label: `${date} ${this.tooltips[i].value}`,
          style: {
            width: "20px",
            height: "20px",
            display: "block",
            backgroundColor: this.tooltips[i].color,
            border: "2px solid white",
            transform: "translate(-8px, -8px)",
          },
          labelStyle: {
            backgroundColor: this.tooltips[i].color,
            "border-radius": "40px",
            border: "2px solid white",
            "margin-top": "15px",
            padding: "10px",
            color: "white",
          },
        };
      });
      return res;
    },
  },
};
</script>

