<template>
  <div id="app">
    <Map class="map_videoclip" :layers="layers" />
    <Control
      class="control_videoclip"
      @update-time="UpdateMap"
      :dates="dates"
      :tooltips="tooltips"
    />
  </div>
</template>

<script>
import Control from "./components/Control";
import Map from "./components/Map";
import { Download } from "./scripts/Download";
import { CreateLayers } from "./scripts/Download";
import { GetData } from "./scripts/Download";
import { CreateDates } from "./scripts/Download";
import { CreateColoredTooltips } from "./scripts/Download";

export default {
  name: "App",
  data() {
    return {
      receivedData: [],
      layers: [],
      tooltips: [],
      dates: [],
    };
  },
  components: {
    Map,
    Control,
  },
  mounted() {
    CreateDates().then((dates) => {
      Download(dates).then((data) => {
        this.dates = dates;
        this.receivedData = data;
        this.tooltips = CreateColoredTooltips(data, dates);
        const timeData = GetData(data, dates[0]);
        this.layers = CreateLayers(timeData);
        this.load = true;
      });
    });
  },
  methods: {
    UpdateMap(index) {
      const timeData = GetData(this.receivedData, index);
      this.layers = CreateLayers(timeData);
    },
  },
};
</script>

