<template>
  <vl-map>
    <vl-view
      :zoom.sync="zoom"
      :center.sync="center"
      :rotation.sync="rotation"
    ></vl-view>
    <vl-layer-tile id="osm">
      <vl-source-osm></vl-source-osm>
    </vl-layer-tile>
    <vl-layer-vector>
      <vl-feature v-for="layer in layers" :key="layer.id">
        <vl-geom-point
          :coordinates="layer.coordinates"
          matrix-set="EPSG:3857"
        ></vl-geom-point>
        <vl-style-box>
          <vl-style-circle :radius="Number(layer.radius)">
            <vl-style-fill :color="layer.fill"></vl-style-fill>
            <vl-style-stroke
              :color="layer.stroke.color"
              :width="Number(layer.stroke.width)"
            ></vl-style-stroke>
          </vl-style-circle>
          <vl-style-text :text="layer.value + ''" font="14px sans-serif">
            <vl-style-fill color="white"></vl-style-fill>
          </vl-style-text>
        </vl-style-box>
      </vl-feature>
    </vl-layer-vector>
  </vl-map>
</template>

<script>
export default {
  props: ["layers"],
  data() {
    return {
      zoom: 11,
      center: [10326243.3178926, 7555742.7863465175],
      rotation: 0,
    };
  },
};
</script>

